let videosIndex = 0;

class Preview {
    renderItems(item) {  // eslint-disable-line
        // data.forEach((item, index) => {

        videosIndex += 1;
        return `<div class="preview-block" id="${videosIndex}">
            <div class="imgBlock">
                <img src="${item.previewUrl}" class="preview-img">
                <h2 class="preview-title">
                    <a href="https://www.youtube.com/watch?v=${item.previewId}">${item.previewTitle}</a>
                </h2>
            </div>
            <ul class="info-box">
                <li><b>Date: </b>${item.previewDate}</li>
                <li><b>Author: </b>${item.previewAuthor}</li>
                <li><b>Viewed: </b>${item.previewViewers}</li>
            </ul>
            <p class="preview-description">${item.previewDesc}</p>
            </div>`;

        // return videoItem;
        // });

        // MAIN_BLOCK.innerHTML += str;
    }
}


export default new Preview();



