import constants from '../constants';

const { BASE_URL, KEY } = constants;

class GetVideo {
    requestMain(query, pageToken) { // eslint-disable-line
        return fetch(`${BASE_URL}search?pageToken=${pageToken}&key=${KEY}&type=video&part=snippet&maxResults=15&q=${query}`)
            .then(res => res.json());
    }

    requestStatistics(id) { // eslint-disable-line
        return fetch(`${BASE_URL}videos?key=${KEY}&id=${id}&part=snippet,statistics`)
            .then(res => res.json());
        // .then((data) => data.items[0].statistics.viewCount);
    }
}
export default GetVideo;



